<template>
  <div class="hamburger-menu">
    <div class="menu-button">
      <i class="material-icons" @click="expandMenu">menu</i>
    </div>
    <div :class="(expanded ? 'menu menu-expanded' : 'menu')">
      <div class="close-menu-button">
        <i class="material-icons" @click="closeMenu">close</i>
      </div>
      <div class="links">
        <a @click="goTo($event)" data-href="/">Inicio</a>
        <a @click="goTo($event)" data-href="/acerca-de-mi">Acerca de mi</a>
        <a @click="goTo($event)" data-href="/proyectos">Proyectos</a>
        <a @click="goTo($event)" data-href="/contacto">Contacto</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      expanded: false
    }
  },
  methods: {
    closeMenu () {
      this.expanded = false
    },
    expandMenu () {
      this.expanded = true
    },
    goTo (event) {
      var url = event.target.dataset.href
      console.log(url)
      document.querySelector('.screen').classList.remove('screen-hide')
      setTimeout(() => {
        this.$router.push(url)
      }, 1500)
    }
  },
  name: 'hamburger-menu'
}
</script>

<style lang="scss" scoped>
.hamburger-menu {
  position: fixed;
  display: flex;
  align-items: center;
  top: 0px;
  right: 36px;
  height: 60px;
  z-index: 50;
}

.menu-button {
  position: absolute;
  z-index: 1;
  color: #FBFBFF;
  cursor: pointer;
}

.blue .menu-button {
  color: #00BBFF;
}

.blue .menu {
  color: #FBFBFF;
  background-color: #00BBFF;

  .links {
    a {
      color: #FBFBFF;
    }
  }
}

.close-menu-button {
  cursor: pointer;
  margin-left: 12px;
}

.menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 2;
  position: fixed;
  left: 100%;
  height: 60px;
  width: 100%;
  background-color: #FBFBFF;
  color: #00BBFF;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.5);
  transition: left 1s;

  .links {
    margin-left: auto;

    a {
      margin-right: 12px;
      text-transform: uppercase;
      font-family: 'Nunito Sans';
      font-weight: bold;
      color: #00BBFF;
      text-decoration: none;
      user-select: none;
      cursor: pointer;
    }
  }
}

.menu-expanded {
  left: 0px;
}
</style>
